import React from 'react';

function Header() {
    return (
        <header style={headerStyle}>
            <h1 className="margin">Todo List</h1>
            <p className="margin">by Arwin Chua</p>
        </header>
    )
    
}

const headerStyle = {
    background: 'rgb(255,140,0)',
    color: '#fff',
    textAlign: 'center',
    padding: '10px'
}

export default Header;